/* eslint-disable eqeqeq */
import { h, Component } from 'preact';
import { bindActionCreators } from 'redux';
import { connect } from 'preact-redux';
import style from './style.scss';
import { getEthSatus } from '../../../api/explorer';
import { setEthState } from '../../../actions/block';

class Body extends Component {

	handleScroll = (e) => {
		(window.pageYOffset || document.documentElement.scrollTop) ?
			this.state.isTop && this.setState({ isTop: false })
			: !this.state.isTop && this.setState({ isTop: true });
	}


	toggleMobileMenu = () => this.setState(prev => ({ mobileMenuOpened: !prev.mobileMenuOpened }));

	getCurrentBlock = () => {
		getEthSatus((data) => {
			this. props.setEthState(data);
			setTimeout(() => {
				this.getCurrentBlock();
			}, 4000);
		});
	}

	getDiffTimeStr(timeVal){
		let now = Math.round(new Date().getTime()/1000);
		let diff = (now - timeVal);
		let result = `${diff} seconds ago`;
		if (diff > 60 ){
			result = `${Math.round(diff/60)} minutes ago`;
		}

		if (diff > 3600 ){
			result = `${Math.round(diff/3600)} hours ago`;
		}

		return result;
	}

	constructor(props){
		super(props);
		this.state = {
			blocks: []
		};
	}
    
	componentDidMount(){
		
		// window.addEventListener('scroll', this.handleScroll);
		this.getCurrentBlock();
		
	}

	componentWillUnmount(){
		window.removeEventListener('scroll', this.handleScroll);
		
	}

	render() {
		
		const blocks = this.props.blocks;

		return (
			<div class={`${style.fanEthStatsContainer}`} >
				<table class="uk-table uk-table-divider">
					<thead>
						<tr>
							<th>Block Number</th>
							<th>Block Hash</th>
							<th>Time Stamp</th>
						</tr>
					</thead>
					<tbody>
						{
							blocks.map((val, index) => (
								<tr>
									<td>#{val.number}</td>
									<td>{val.hash}</td>
									<td>{this.getDiffTimeStr(val.timestamp)}</td>
								</tr>
							))
						}
					</tbody>
				</table>
			</div>
		);
	}
}


const mapStateToProps = (state) => ({
	blocks: state.block.blocks
});


const mapDispatchToProps = dispatch => bindActionCreators({
	setEthState
}, dispatch);


export default connect(
	mapStateToProps,
	mapDispatchToProps
)(Body);


