import { h, Component } from 'preact';
import { bindActionCreators } from 'redux';
import { connect } from 'preact-redux';
import { getBlock } from '../../api/explorer';
import style from './style.scss';
import { Link } from 'preact-router';

class Blockdetail extends Component {

	constructor(props){
		super(props);
		this.state = {
			block: null,
			address: this.props.address
		};
	}

	componentWillMount(){
		
		getBlock(this.state.address, (data) => {
			this.setState({ block: data });
		});
	}
	
	render() {
		const block = this.state.block;
		if (block){
			return (
				<div class={`uk-card uk-card-default ${style.fanEthBlockDetail}`}>
					<div class="uk-card-header">
						<div class="uk-grid-small uk-flex-middle" uk-grid>
							<div>{block.hash}</div>
						</div>
					</div>
					<div class="uk-card-body">
						<div class={style.row}>
							<div class={style.col3}>
								number
							</div>
							<div class={style.col9}>
								{block.number}
							</div>
						</div>
						<div class={style.row}>
							<div class={style.col3}>
								nonce
							</div>
							<div class={style.col9}>
								{block.nonce}
							</div>
						</div>
						<div class={style.row}>
							<div class={style.col3}>
								parentHash
							</div>
							<div class={style.col9}>
								{block.parentHash}
							</div>
						</div>
						<div class={style.row}>
							<div class={style.col3}>
								size
							</div>
							<div class={style.col9}>
								{block.size}
							</div>
						</div>
						<div class={style.row}>
							<div class={style.col3}>
								transactions
							</div>
							<div class={style.col9}>
								{block.transactions.length}
							</div>
						</div>
					</div>
					<div class="uk-card-footer">
						<Link href="/" class="uk-button uk-button-text">Back</Link>
					</div>
				</div>
			);
		}
		
		return (<div>loading...</div>);
		
		
	}
}


const mapStateToProps = (state) => ({
	userData: state.loggedInUser,
	locale: state.locale
});


const mapDispatchToProps = dispatch => bindActionCreators({
	
}, dispatch);


export default connect(
	mapStateToProps,
	mapDispatchToProps
)(Blockdetail);


