import { apiUrls } from './index';
import axios  from 'axios';

export function getEthSatus(callback){
	axios({
		method: 'get',
		url: apiUrls.GET_ETH_STATS,
		responseType: 'json',
		auth:{
            username: 'username',
            password: 'password'
        }
	}).then((response) => {
		callback(response.data);
	});
}


export function getBlock(address, callback){
	axios({
		method: 'get',
		url: apiUrls.GET_BLOCK,
		responseType: 'json',
		params: {
			blockNumber: address
		},
		auth:{
            username: 'username',
            password: 'password'
        }
	}).then((response) => {
		callback(response.data);
	});
}

